import React, { Component } from "react";
import "./service.css";
class Service extends Component {
  state = {
    data: [
      { name: "frontend", imgsrc: require("../assets/image/1.png") },
      { name: "backend", imgsrc: require("../assets/image/2.png") },
      { name: "App", imgsrc: require("../assets/image/3.png") },
      { name: "mini programs", imgsrc: require("../assets/image/4.png") },
    ],
  };
  render() {
    return (
      <div className="service">
        {this.state.data.map((i) => (
          <div className="serviceitem" key={i.name}>
            <img alt={i.name} src={i.imgsrc}></img>
            <span className="servicename">{i.name}</span>
          </div>
        ))}
      </div>
    );
  }
}

export default Service;
