import React, { Component } from "react";
import "./skill.css";
class Skill extends Component {
  state = {
    skill: [
      "react/vue/angular",
      "express/springboot",
      "TS/dart",
      "flutter/ionic",
    ],
  };
  render() {
    return (
      <div className="skillcon">
        {this.state.skill.map((i) => (
          <div className="skillinner" key={i}>
            <span className="skillinnertext">{i}</span>
          </div>
        ))}
      </div>
    );
  }
}

export default Skill;
